"use client";

import { useState } from "react";
import { useRouter } from "next/navigation";
import { useForm } from "react-hook-form";
import { zodResolver } from "@hookform/resolvers/zod";
import * as z from "zod";
import Link from "next/link";
import { Loader2 } from "lucide-react";

import { Button } from "./button";
import {
	Form,
	FormControl,
	FormField,
	FormItem,
	FormLabel,
	FormMessage,
} from "./form";
import { Input } from "./input";
import { useToast } from "../hooks/useToast";

const formSchema = z.object({
	email: z
		.string()
		.email({ message: "Please enter a valid email." })
		.endsWith("@UWM.edu", {
			message: "Must be a valid University Email Address.",
		}),
	password: z.string().min(1, { message: "Password is required." }),
});

export default function LoginForm() {
	const [isLoading, setIsLoading] = useState(false);
	const router = useRouter();
	const { toast } = useToast();

	const form = useForm<z.infer<typeof formSchema>>({
		resolver: zodResolver(formSchema),
		defaultValues: {
			email: "",
			password: "",
		},
	});

	async function onSubmit(values: z.infer<typeof formSchema>) {
		setIsLoading(true);
		// Simulate API call
		await new Promise((resolve) => setTimeout(resolve, 1000));

		if (values.email === "test@UWM.edu" && values.password === "password") {
			toast({
				title: "Success",
				description: "Login successful! Redirecting...",
				duration: 1500, // milliseconds
			});
			setTimeout(() => {
				router.push("/homepage");
			}, 1500);
		} else {
			toast({
				variant: "destructive",
				title: "Login Failed",
				description: "Incorrect email or password. Please try again.",
			});
			form.reset({ ...values, password: "" });
		}
		setIsLoading(false);
	}

	return (
		<Form {...form}>
			<form onSubmit={form.handleSubmit(onSubmit)} className="space-y-6">
				<div className="space-y-4">
					<FormField
						control={form.control}
						name="email"
						render={({ field }) => (
							<FormItem>
								<FormLabel>University Email Address</FormLabel>
								<FormControl>
									<Input
										placeholder="epantherID@uwm.edu"
										{...field}
										disabled={isLoading}
										autoComplete="email"
									/>
								</FormControl>
								<FormMessage />
							</FormItem>
						)}
					/>
					<FormField
						control={form.control}
						name="password"
						render={({ field }) => (
							<FormItem>
								<div className="flex items-center">
									<FormLabel>Password</FormLabel>
									<Link
										href="/forgot-password"
										className="ml-auto inline-block text-sm underline text-muted-foreground hover:text-primary"
									>
										Forgot password?
									</Link>
								</div>
								<FormControl>
									<Input
										type="password"
										{...field}
										disabled={isLoading}
										autoComplete="current-password"
										placeholder="••••••••"
									/>
								</FormControl>
								<FormMessage />
							</FormItem>
						)}
					/>
				</div>
				<Button type="submit" className="w-full" disabled={isLoading}>
					{isLoading && <Loader2 className="mr-2 h-4 w-4 animate-spin" />}
					Sign In
				</Button>
			</form>
		</Form>
	);
}
